import React from 'react';

export const netlifyToGatsby = (Template) => {
    return class Transformed extends React.Component {
        constructor(props) {
            super(props);

            this.cacheTemplate = this.cacheTemplate.bind(this);
            this.autoFocus = this.autoFocus.bind(this);
        }

        cacheTemplate() {
            const ref = document.querySelector('.cms__EntryEditor__previewPane iframe');

            const inputs = ref.contentDocument.querySelectorAll('input');

            [].forEach.call(inputs, (input) => {
                input.disabled = true;
            });

            document.addEventListener('keydown', this.autoFocus);
        }

        autoFocus(e) {
            if (e.key === 'Tab') return;

            setTimeout(() => {
                e.target.focus();
            });
        }

        componentWillUnmount() {
            document.removeEventListener('keydown', this.autoFocus);
        }

        transformedProps() {
            const { entry } = this.props;

            const { data } = entry.toJS();

            // Set previewing to true to ensure breaking elements don't render
            // For example `Link` won't work because router is not in context
            for (let key in data) {
                if (typeof (data[key]) === 'object') {
                    if (data.hasOwnProperty(key) && data[key]) {
                        data[key]['previewMode'] = true;
                    }
                }
            }

            return {
                data: {
                    markdownRemark: {
                        html: data.body,
                        frontmatter: {
                            ...data
                        }
                    }
                }
            };
        }

        render() {
            return <Template ref={ this.cacheTemplate } { ...this.transformedProps() } />
        }
    }
};



